:root {
        --companies-bg: #f5f7fa;
        --companies-text: #0B1D39;
        --transition: 0.3s ease;
        --border-radius: 8px;
        --white: #fff;
      }

      .companies {
        padding: 4rem 0;
        background: var(--companies-bg);
        color: var(--companies-text);
      }

      .companies .section-header h2 {
        text-align: center;
        margin-bottom: 2rem;
      }

      .section-header h2 {

        color: #090909;
        /* dark gray */
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 600;
        text-align: center;
        margin-bottom: 20px;
      }

      .companies-scroll {
        overflow: hidden;
        mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
      }

      .companies-track {
        display: flex;
        gap: 3rem;
        animation: scroll 30s linear infinite;
        align-items: center;
      }

      .company-logo {
        flex-shrink: 0;
        width: 120px;
        height: 60px;
        background: var(--white);
        border-radius: var(--border-radius);
        padding: 0.75rem;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .company-logo img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
        filter: grayscale(100%);
        transition: filter var(--transition);
      }

      .company-logo:hover img {
        filter: grayscale(0%);
      }

      @keyframes scroll {
        0% {
          transform: translateX(0);
        }

        100% {
          transform: translateX(-50%);
        }
      }